import { put, delay } from 'redux-saga/effects';
import { changeSystemField } from '../../../../AC/system';

function* fail({ status }) {
  if (status === 404) {
    yield put(changeSystemField('snackbar', '!   .'));
  }

  if (status === 500) {
    yield put(changeSystemField('snackbar', ' !  .'));
  }

  yield delay(4000);
  yield put(changeSystemField('snackbar', ''));
}

export default fail;
